html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  width: 100%;
  height: 100%;
}

.header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 30%;
  background-color: #0072C6;
}

.header-link {
  display: flex;
  align-items: center;
  font-size: 3rem;
  font-weight:800;
  padding: 20px;
  text-decoration: none;
  color:white;
}

.signin {
  display: flex;
  flex-direction: column;
  align-items: center;
}

form {
  display:flex;
  flex-direction: column;
  align-items: center;
  border: solid;
  padding: 10px;
}

form input {
  width: 50%;
  min-width: 400px;
  padding: 10px;
}

form button {
  width: 30%;
  min-width: 200px;
  padding: 10px;
}

form input:not(:last-child) {
  margin-bottom: 10px;
} 

.error {
  color: red;
}

.home {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 20px;
}

.center {
  align-self: center;
}

.drawingComponent {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.drawableCanvas {
  display: flex;
  justify-content: center;
  border-style: solid;
}

.canvas {
  border-style: solid;
}

.canvas:hover {
  border-color: green;
}

.toolbar {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
}

.tool {
  margin: 10px;
  flex: 1;
}

.gallery {
  display: flex;
  flex-direction: row;
  flex-wrap:wrap;
  justify-content: space-evenly;
}

.gallery > div {
  margin: 5px;
}

.loading {
  display: flex;
  height: 30%;
  width: 100%;
  align-items: center;
  justify-content: center;
}

button {
  padding: 10px;
  min-width: 100px;
}